import React from 'react'
import { MenuItem, MenuList } from '../lib/core'
import { Router } from '@reach/router'
import { PageProps } from '../lib'

const NavBar = ({ navigate }: PageProps) => (
  <MenuList style={{ display: 'flex', flexDirection: 'row' }}>
    <MenuItem onClick={() => (navigate as any)('/')}>Home</MenuItem>
    <MenuItem onClick={() => (navigate as any)('/textfield')}>
      TextField
    </MenuItem>
    <MenuItem onClick={() => (navigate as any)('/selections')}>
      Selections
    </MenuItem>
  </MenuList>
)

export default () => (
  <Router primary={false}>
    <NavBar path='*' />
  </Router>
)
